@include tablet{
    .seccion_inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.blog_contenedor{
    
    margin-bottom: 2rem;
    &:last-of-type{
        margin-bottom: 0;
    }
    a{
        color: black;
        span{
            color: $amarillo;
        }
    }
    h4{
        margin: 0;
        line-height: 1.4;
        &::after{
            width: 15rem;
            margin-top: 1rem;
            content: "";
            display: block;
            height: 0.5rem;
            background-color: $verde;
        }
    }
    
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    .blog_contenido{
        h4{
            margin: 0;
        }
    }
}

.testimonial{
    border-radius: 2rem;
    font-size: 2.4rem;
    padding: 2rem;
    
    blockquote, p{
        color: $blanco;
    }
    blockquote{
        padding-left: 5rem;
        position: relative;
        font-size: 2.4rem;
        &::before{
            content: "";
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
        }
    }
    background-color: $verde;
}

