.formulario{
    fieldset{
        display: flex;
        flex-direction: column;
        legend{
            font-size: 2rem;
        }
        label{
            font-weight: $bold;
            text-transform: uppercase;
        }
        textarea{
            height: 20rem;
        }
        input,textarea,select{
            margin-bottom: 2rem;
            border: 1px solid $gris;
            border-radius: 1rem;
            background-color: $blanco;
            padding: 1rem;
        }
        p{
            font-size: 1.4rem;
            margin-bottom: 0;
        }
        .form-contactar{
            display: flex;
            justify-content: space-between;
            align-items: center;
            max-width: 30rem;
            input{
                margin: 0;
            }
        }
    }
}

