.webp .bgcontacto{
    background-image: url(../img/encuentra.webp);
}
.no-webp .bgcontacto{
    background-image: url(../img/encuentra.jpg);
}

.bgcontacto{
    background-position: 50%;
    background-size: cover;
    padding: 10rem 0;
}

.contactoindex{
    display:flex;
    flex-direction: column;
    align-items: center;
    h1,p{
        color: $blanco; 
    }
    h1{
        font-size: 4rem;
        font-weight: 900;
    }
    p{
       font-size: 1.8rem;
    }
}
