.no-webp .inicio{
    background-image: url(../img/header.jpg);
}

.webp .inicio{
    background-image: url(../img/header.webp);
}

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 5rem 0;
    &.inicio{
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        @include tablet{
            min-height: 70rem;
        }
        .contenido-header{
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}
.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h1{
        color: $blanco;
        text-align: left;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}
.dark-mode-icon{
    width: 5rem;
    height: 5rem;
    margin-top: 3rem;
    margin-bottom: 1rem;
    filter: invert(100%);
    @include tablet{
        margin-top: 2rem;
    }
}
.darkmode-nav{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include tablet{
        align-items: flex-end;
    }
    &:hover{
       cursor: pointer; 
    }
}
