.contenedor-anuncios{
    @include tablet{
        @include gridColumn(3,2rem);
    }
}
.anuncio{
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 10);
    margin-bottom: 2rem;
    .precio{
        font-weight: $bold;
        color: $verde;
        font-size: 3rem;
    }
    h3, p{
        
        margin: 0;
    }
    
}
.anuncio-contenido{
    padding: 2rem;
}
.anuncio-iconos{
    display: flex;
    list-style: none;
    padding: 0;
    li{
        display: flex;
        flex: 1;
        img{
            flex-basis: 3rem;
            margin-right: 1rem;
        }
        p{
            font-weight: $black;
        }
    }
}
