.barra {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        flex-basis: auto;
    }

    a img {
        width: 30rem;
    }
}

.barra-nav {
    
    opacity: 0;
    // transition: opacity 1s linear;
    height: 0rem;
    @include tablet {
        visibility: visible ;
        opacity: 1;
        display: flex;
        flex-direction: row;
    }

    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        &:hover {
            color: rebeccapurple;
        }

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type {
                margin: 0;
            }
        }
    }
}

.mobile-menu {
    img {
        cursor: pointer;
        margin-top: 2rem;
        width: 5rem;
    }

    @include tablet {
        display: none;
    }
}

.mostrar {
    display: block;
    opacity: 1;
    height: auto;
    @include tablet {
        display: flex;
        flex-direction: row;
    }
}