/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin gridColumn($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin gridRow($rows, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-rows: repeat($rows, 1fr);
}
@mixin boton($color,$bgcolor,$display){
    color: $color;
    background-color: $bgcolor;
    display: $display;
    border: none;
    text-align: center;
    padding: 1rem 3rem;
    font-weight: $bold;
    margin-top: calc($separacion/2);

    &:hover{
        background-color: darken($bgcolor,5);
        cursor: pointer;
    }
}
