.darkmode {
    background-color: $negro;

    p,
    a,
    h1,
    h2,
    h3,
    h4,
    legend,
    label,
    blockquote {
        color: $blanco;
    }
    .formulario fieldset input,.formulario fieldset textarea,.formulario fieldset select{
        color: $blanco;
        background-color: $grisOscuro;
        border-color: $grisOscuro;
    }
    .formulario fieldset{
        border-color: $grisOscuro;
    }
    
    .anuncio{
        background-color: $grisOscuro;
        border: none;
        .anuncio-iconos{
            img{
                filter: invert(100%);
            }
        }
    }
}