}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.padding {
  padding: 2rem;
}

.boton-amarillo-inlineblock {
  color: #FFFFFF;
  background-color: #E08709;
  display: inline-block;
  border: none;
  text-align: center;
  padding: 1rem 3rem;
  font-weight: 700;
  margin-top: 2.5rem;
}
.boton-amarillo-inlineblock:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  color: #FFFFFF;
  background-color: #E08709;
  display: block;
  border: none;
  text-align: center;
  padding: 1rem 3rem;
  font-weight: 700;
  margin-top: 2.5rem;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde-inlineblock {
  color: #FFFFFF;
  background-color: #71B100;
  display: inline-block;
  border: none;
  text-align: center;
  padding: 1rem 3rem;
  font-weight: 700;
  margin-top: 2.5rem;
}
.boton-verde-inlineblock:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-verde-block {
  color: #FFFFFF;
  background-color: #71B100;
  display: block;
  border: none;
  text-align: center;
  padding: 1rem 3rem;
  font-weight: 700;
  margin-top: 2.5rem;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

/** UI **/
.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .contenido-footer .barra-nav {
  opacity: 1;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 5rem 0;
}
.header.inicio {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.dark-mode-icon {
  width: 5rem;
  height: 5rem;
  margin-top: 3rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .dark-mode-icon {
    margin-top: 2rem;
  }
}

.darkmode-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .darkmode-nav {
    align-items: flex-end;
  }
}
.darkmode-nav:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    flex-basis: auto;
  }
}
.barra a img {
  width: 30rem;
}

.barra-nav {
  opacity: 0;
  height: 0rem;
}
@media (min-width: 768px) {
  .barra-nav {
    visibility: visible;
    opacity: 1;
    display: flex;
    flex-direction: row;
  }
}
.barra-nav a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.barra-nav a:hover {
  color: rebeccapurple;
}
@media (min-width: 768px) {
  .barra-nav a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .barra-nav a:last-of-type {
    margin: 0;
  }
}

.mobile-menu img {
  cursor: pointer;
  margin-top: 2rem;
  width: 5rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mostrar {
  display: block;
  opacity: 1;
  height: auto;
}
@media (min-width: 768px) {
  .mostrar {
    display: flex;
    flex-direction: row;
  }
}

.iconosindex-contenido {
  display: block;
}
@media (min-width: 768px) {
  .iconosindex-contenido {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.iconosindex-info img {
  height: 10rem;
}
.iconosindex-info h3 {
  text-transform: uppercase;
}
.iconosindex-info p {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.anuncio .precio {
  font-weight: 700;
  color: #71B100;
  font-size: 3rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.anuncio-contenido {
  padding: 2rem;
}

.anuncio-iconos {
  display: flex;
  list-style: none;
  padding: 0;
}
.anuncio-iconos li {
  display: flex;
  flex: 1;
}
.anuncio-iconos li img {
  flex-basis: 3rem;
  margin-right: 1rem;
}
.anuncio-iconos li p {
  font-weight: 900;
}

.webp .bgcontacto {
  background-image: url(../img/encuentra.webp);
}

.no-webp .bgcontacto {
  background-image: url(../img/encuentra.jpg);
}

.bgcontacto {
  background-position: 50%;
  background-size: cover;
  padding: 10rem 0;
}

.contactoindex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contactoindex h1, .contactoindex p {
  color: #FFFFFF;
}
.contactoindex h1 {
  font-size: 4rem;
  font-weight: 900;
}
.contactoindex p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .seccion_inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog_contenedor {
  margin-bottom: 2rem;
}
.blog_contenedor:last-of-type {
  margin-bottom: 0;
}
.blog_contenedor a {
  color: black;
}
.blog_contenedor a span {
  color: #E08709;
}
.blog_contenedor h4 {
  margin: 0;
  line-height: 1.4;
}
.blog_contenedor h4::after {
  width: 15rem;
  margin-top: 1rem;
  content: "";
  display: block;
  height: 0.5rem;
  background-color: #71B100;
}
@media (min-width: 768px) {
  .blog_contenedor {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog_contenedor .blog_contenido h4 {
  margin: 0;
}

.testimonial {
  border-radius: 2rem;
  font-size: 2.4rem;
  padding: 2rem;
  background-color: #71B100;
}
.testimonial blockquote, .testimonial p {
  color: #FFFFFF;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
  font-size: 2.4rem;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}

.darkmode {
  background-color: #000000;
}
.darkmode p,
.darkmode a,
.darkmode h1,
.darkmode h2,
.darkmode h3,
.darkmode h4,
.darkmode legend,
.darkmode label,
.darkmode blockquote {
  color: #FFFFFF;
}
.darkmode .formulario fieldset input, .darkmode .formulario fieldset textarea, .darkmode .formulario fieldset select {
  color: #FFFFFF;
  background-color: #333333;
  border-color: #333333;
}
.darkmode .formulario fieldset {
  border-color: #333333;
}
.darkmode .anuncio {
  background-color: #333333;
  border: none;
}
.darkmode .anuncio .anuncio-iconos img {
  filter: invert(100%);
}

/** internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-size: 2.2rem;
  font-weight: 900;
  margin: 3rem 0 0 0;
}

.entrada {
  display: block;
}

.formulario fieldset {
  display: flex;
  flex-direction: column;
}
.formulario fieldset legend {
  font-size: 2rem;
}
.formulario fieldset label {
  font-weight: 700;
  text-transform: uppercase;
}
.formulario fieldset textarea {
  height: 20rem;
}
.formulario fieldset input, .formulario fieldset textarea, .formulario fieldset select {
  margin-bottom: 2rem;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  background-color: #FFFFFF;
  padding: 1rem;
}
.formulario fieldset p {
  font-size: 1.4rem;
  margin-bottom: 0;
}
.formulario fieldset .form-contactar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 30rem;
}
.formulario fieldset .form-contactar input {
  margin: 0;
}